:root {
  --primary: rgba(221,221,221,255);
}

.btn {
  padding: 8px 20px;
  border-radius: 6px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: var(--primary);
  color: #242424;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: rgb(0, 0, 0);
  padding: 8px 20px;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  margin-top: 20px;
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  margin-top: 15px;
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: rgb(10, 10, 10);
  color: #ffffff;
  transition: 250ms;
}
