@import url(https://fonts.googleapis.com/css2?family=Montserrat&family=Roboto+Mono:wght@300&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-family: 'Roboto Mono', monospace;
}

body {
  background-color: rgba(221,221,221,255);
}

.App {
  text-align: center;
}

.App-header {
  min-height: 80vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  font-size: calc(10px + 2vmin);
}

.mint {
  margin-top: 10px;
  margin-bottom: 20px;
}

.mintMessage{
  color: red;
  font-size: 15px;
}

.turks{
  color:red;
}

.descriptionContainer{
  width: 500px;
  border: 7px solid rgb(14, 15, 14);
  padding: 50px;
  margin: auto;
  margin-top: 130px;
  border-radius: 5px;
}
.description{
  margin-top: 10px;
}
:root {
  --primary: rgba(221,221,221,255);
}

.btn {
  padding: 8px 20px;
  border-radius: 6px;
  outline: none;
  border: none;
  cursor: pointer;
}

.btn--primary {
  background-color: rgba(221,221,221,255);
  background-color: var(--primary);
  color: #242424;
  border: 1px solid rgba(221,221,221,255);
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: rgb(0, 0, 0);
  padding: 8px 20px;
  border: 1px solid rgba(221,221,221,255);
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  margin-top: 20px;
  padding: 8px 20px;
  font-size: 18px;
}

.btn--large {
  margin-top: 15px;
  padding: 12px 26px;
  font-size: 20px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: rgb(10, 10, 10);
  color: #ffffff;
  transition: 250ms;
}

.navbar {
  background: rgba(221,221,221,255);
  height: 80px;
  display: flex;
  justify-content: right;
  align-items: right;
  margin-right: 30px;
  font-size: 1.2rem;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
}

.title{
  font-size: 50px;
  border: 7px solid rgb(14, 15, 14);
  width: 300px;
  margin: auto;
  border-radius: 5px;
}







